const defaultState = {
  data: {
    launches: []
  }
};
export const appReducer = (state = defaultState, action) => {
  switch (action.type) {
    case "ADD_LAUNCHES":
      return {
        ...state,
        data: action.value
      };
    default:
      return state;
  }
};



